.header {
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header a {
    color: #ffffff;
    text-decoration: none;
}

.header_logo {
    width: 100px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 18px;
}

.header_nav {
    display: flex;
}

.header_link {
    color: white;
    text-decoration: none;
}

.header_option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header_optionLineOne {
    font-size: 10px
}

.header_optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header_optionCart {
    display: flex;
}

.header_cartCount {
    margin-left: 10px;
    margin-right: 10px;
}

.header_search {
    display: flex;
    /* flex: 1; 
     */
}

.header_searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_searchIcon {
    background-color: #cd9042;
    color: black;
    height: 12px;
}