.payment {
    background-color: white;
}

.payment_container>h1 {
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: lightgrey;
    border-bottom: 1px solid darkgrey;
}

.payment_container>h1 a {
    text-decoration: none;
}

.payment_section {
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid darkgrey;
}

.payment_title {
    flex: 0.2;
}

.payment_address,
.payment_items,
.payment_details {
    flex: 0.8;
}