.products {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.products li {
    list-style-type: none;
    padding: 0;
    flex: 0 1 34rem;
    margin: 1rem;
    height: 50rem;
    border-bottom: .1rem #c0c0c0 solid;
}

.product {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.product-name {
    font-size: 2rem;
    font-weight: bold;
}

.product-price {
    font-size: 2.5rem;
    font-weight: bold;
}

.product-brand {
    font-size: 1.2rem;
    color: #808080;
}

.product-image {
    max-width: 34rem;
    max-height: 34rem;
    border-radius: 4%;
}

.product-rating {
    margin-bottom: 1rem;
}